import Vue from 'vue'
import ColumnRender from './ColumnRender'
import ElTableFix from './ElTableFix'

// 
const defaultPager = {
  currentPage: 1,
  pageSizes: [10, 20, 40, 50, 100],
  pageSize: 20,
  layout: 'total, sizes, ->, prev, pager, next, jumper',
  background: true,
  total: 0
}

/*  */
export default {
  name: 'ElTableExt',

  components: { ColumnRender, ElTableFix },

  props: {
    // 
    columns: {
      type: Array,
      default: () => []
    },

    // 
    data: {
      type: Array,
      default: () => []
    },

    // 
    query: Function,

    //  false 
    pager: {
      type: [Object, Boolean],
      default: () => ({})
    },

    // 
    autoLoad: {
      type: Boolean,
      default: true
    },

    // 
    defaultSort: Object,

    // 
    height: String,

    // 
    emptyText: String,

    //  el-table 
    options: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      mounted: false,
      loading: false, //  loading
      queryData: null, // 
      errorMessage: '', // 
      pagin: this.getPagin(), // 
      sorts: this.defaultSort || {}, // 
      filters: {} // 
    }
  },

  computed: {
    // 
    isStatic() {
      return typeof this.query !== 'function'
    },

    // 
    // TODO: 
    tableData() {
      return (this.isStatic ? this.data : this.queryData) || []
    },

    // 
    selection() {
      return this.mounted ? this.$refs.table.selection : []
    },

    // 
    cols() {
      return this.columns.map(item => {
        if (item.renderOption) {
          this.$set(item, 'render', this.getOptionRender(item.renderOption))
        } else if (typeof item.formatter === 'string') {
          const formatter = this.getFilterFormatter(item.formatter)
          if (formatter) {
            this.$set(item, 'formatter', formatter)
          } else {
            this.$delete(item, 'formatter')
          }
        }
        return item
      })
    }
  },

  // 
  watch: {
    // 
    columns: {
      handler() {
        this.$nextTick().then(this.adjust)
      },
      immediate: true
    },

    // 
    tableData: {
      handler() {
        this.$nextTick().then(this.adjust)
      },
      immediate: true
    }
  },

  // 
  activated() {
    this.adjust()
  },

  methods: {
    // ElTable 
    tableMounted() {
      this.mounted = true

      // 
      if (this.pager) {
        this.$el.appendChild(this.$refs.pagin.$el)
      }

      this.filters = this.getFilters()

      // 
      this.autoLoad && this.load()
    },

    // 
    getOptionRender(option) {
      let options =
        typeof option === 'string'
          ? this.$option && this.$option[option]
          : option

      if (!options) {
        console.warn(
          '[FaTable]:  renderOption ',
          option
        )
        return ''
      }

      return (h, { value }) => {
        const match = options.find(item => item.value === value)

        if (match) {
          return <span class={match.class}>{match.text}</span>
        }

        return ''
      }
    },

    // 
    getFilterFormatter(filterName) {
      // 
      const filter = Vue.filter(filterName)

      if (filter) {
        return (row, column, value) => filter(value)
      }
      return undefined
    },

    // /
    bindScope({ row, column, $index: index }) {
      if (row) {
        const value = row[column.property]
        return { row, column, value, index }
      }
      return { column, index }
    },

    // 
    adjust() {
      const { table } = this.$refs
      table && table.doLayout()
    },

    // 
    load() {
      if (this.isStatic) return

      const { pagin, filters, sorts } = this
      this.loading = true

      this.query({ pagin, filters, sorts })
        .then(({ data, total }) => {
          this.queryData = data
          this.errorMessage = ''
          pagin.total = total
        })
        .catch(error => {
          this.queryData = []
          this.errorMessage = error.message
          throw error
        })
        .finally(() => {
          this.loading = false
        })
    },

    // 
    getPagin() {
      if (typeof this.pager === 'boolean') {
        return defaultPager
      }

      return { ...defaultPager, ...this.pager }
    },

    // 
    changeSize(size) {
      Object.assign(this.pagin, {
        pageSize: size,
        currentPage: 1
      })

      this.load()
    },

    // 
    jumpPage(page = 1) {
      this.pagin.currentPage = page
      this.load()
    },

    // 
    sortChange(sort) {
      this.$emit('sort-change', sort)
      if (this.isStatic) return
      this.sorts = sort
      this.jumpPage()
    },

    // 
    getFilters() {
      const filters = {}
      this.$refs.table.columns.forEach(
        ({ property: prop, filteredValue: value }) => {
          if (value && value.length) {
            filters[prop] = value
          }
        }
      )
      return filters
    },

    // 
    filterChange(filters) {
      this.$emit('filter-change', filters)
      if (this.isStatic) return
      this.filters = this.getFilters()
      this.jumpPage()
    }
  }
}
